/**
 * Helper function: Tìm ward mới từ ward cũ (exact match)
 */
export function findNewWard(oldWardName: string): WardMapping | null {
  return WARD_OLD_TO_NEW_MAPPING[oldWardName] || null;
}

/**
 * Helper function: Tìm TẤT CẢ ward cũ (2 cấp) từ ward mới (3 cấp) - TRONG CÙNG TỈNH
 * Dùng khi chuyển đổi 3 cấp → 2 cấp
 * 
 * @param newWardName - Tên ward mới (3 cấp hiện tại)
 * @param provinceName - Tên tỉnh để filter
 */
export function findAllOldWards(newWardName: string, provinceName?: string): WardMapping[] {
  const results: WardMapping[] = [];
  
  // Validate input
  if (!newWardName || !provinceName) {
    return results;
  }
  
  // Normalize để so sánh
  const normalizedNewWard = newWardName.toLowerCase().trim();
  const normalizedProvince = provinceName.toLowerCase().trim();
  
  // Tìm tất cả ward cũ có newWardName khớp
  for (const [key, value] of Object.entries(WARD_OLD_TO_NEW_MAPPING)) {
    // Chỉ lấy ward trong cùng tỉnh
    if (!value.provinceName || value.provinceName.toLowerCase().trim() !== normalizedProvince) {
      continue;
    }
    
    // Kiểm tra newWardName khớp
    if (value.newWardName.toLowerCase().trim() === normalizedNewWard) {
      results.push(value);
    }
  }
  
  return results;
}

/**
 * Helper function: Tìm TẤT CẢ ward mới có chứa tên ward cũ (TRONG CÙNG TỈNH)
 * Dùng cho trường hợp ward bị chia đôi (VD: "Xã Đa Tốn" → 2 kết quả)
 * Dùng khi chuyển đổi 2 cấp → 3 cấp
 * 
 * @param oldWardName - Tên ward cũ (2 cấp)
 * @param provinceName - Tên tỉnh để filter (bắt buộc)
 */
export function findAllNewWards(oldWardName: string, provinceName?: string): WardMapping[] {
  const results: WardMapping[] = [];
  
  // Validate input
  if (!oldWardName || !provinceName) {
    return results;
  }
  
  // Normalize tên tỉnh để so sánh
  const normalizedProvince = provinceName.toLowerCase().trim();
  
  // 1. Tìm exact match trước (trong cùng tỉnh)
  const exactMatch = WARD_OLD_TO_NEW_MAPPING[oldWardName];
  if (exactMatch && exactMatch.provinceName?.toLowerCase().trim() === normalizedProvince) {
    results.push(exactMatch);
  }
  
  // 2. Tìm các key có chứa oldWardName (VD: "Xã Đa Tốn (phần còn lại...)")
  const normalizedSearch = oldWardName.toLowerCase().trim();
  
  for (const [key, value] of Object.entries(WARD_OLD_TO_NEW_MAPPING)) {
    const normalizedKey = key.toLowerCase().trim();
    
    // Skip nếu đã có trong exact match
    if (key === oldWardName) continue;
    
    // Chỉ lấy ward trong cùng tỉnh
    if (!value.provinceName || value.provinceName.toLowerCase().trim() !== normalizedProvince) continue;
    
    // Tìm key bắt đầu bằng oldWardName
    if (normalizedKey.startsWith(normalizedSearch)) {
      results.push(value);
    }
  }
  
  return results;
}

/**
 * Helper function: Lấy tất cả ward cũ của 1 ward mới
 */
export function findOldWards(newWardName: string): string[] {
  return Object.values(WARD_OLD_TO_NEW_MAPPING)
    .filter(m => m.newWardName === newWardName)
    .map(m => m.oldWardName);
}

